.Icon-container {
	cursor: pointer;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 48px;
	width: 48px;
}

.Icon-container--check {
	position: absolute;
	left: 12px;
}

.Icon-container--delete {
	position: absolute;
	top: -24px;
	right: 0;
}

.Icon-svg {
  width: 48px;
  height: 48px;
  transition: fill 0.15s;
	
}

.Icon-container:hover .Icon-svg--check {
	fill: #4caf50;
}

.Icon-container:hover .Icon-svg--delete {
	fill: red;
}

